<script>
  import Section from "./share/section";
  import Section2 from "./share/section2";
  import {exhibti} from"./data";

  export default {

    name: 'grateful',
    components: { Section2 },
    props: {},
    data() {
      return {
        exhibti: exhibti,
        showpop:false,
        onindex:0
      };
    },
 
    activated(){

    },

    beforeUpdate (){

    },

    methods: {

    	changeLine(str){
		  return str.replace(/<br>/g,'\n')
	    },

      mousewheel(e){
       e.preventDefault();
      },

     toTop() {
        let tempHeight =
          document.documentElement.scrollTop || document.body.scrollTop;
        document
          .querySelector(".main-header")
          .scrollIntoView({ behavior: "smooth" });
      },
      openPop(e){
        this.showpop = true,
        this.onindex =e
        document.body.style.overflow='hidden';
      },
      popColse(){
        this.showpop = false
        document.body.style.overflow='';
      },
      pre(){
        if(this.onindex>0){
           this.onindex = this.onindex -1
        }
        
      },
      nex(){
        if(this.onindex < (this.exhibti.length - 1)){
           this.onindex = this.onindex +1
        }
      },


    },
    
    

    mounted() {

      let blocks = document.getElementsByClassName("cd_blocks");
      let offset = 0.93;
      for (let i = 0; i < blocks.length; i++) {
        if (
          blocks[i].getBoundingClientRect().top <=
          window.innerHeight * offset
        ) {
          blocks[i].classList.add("animated", "fadeInUp", "delay-06s");
        }
      }
      if (this.$route.query.to) {
        this.$nextTick(() => {
          document
            .querySelector(`#${this.$route.query.to}`)
            .scrollIntoView({ behavior: "smooth" });
        });
      }
      let self = this;
      
      window.addEventListener(
        "scroll",
        function(event) {
          for (let i = 0; i < blocks.length; i++) {
            if (
              blocks[i].getBoundingClientRect().top <=
              window.innerHeight * offset
            ) {
              blocks[i].classList.add("animated", "fadeInUp", "delay-06s");
            }
          }

          let tempHeight =
            document.documentElement.scrollTop || document.body.scrollTop;
          if (tempHeight > 320) {
            document.querySelector(".back-top").classList.add("mobile-hide");
          } else {
            document.querySelector(".back-top").classList.remove("mobile-hide");
          }
        },
        true
      );
      

                
    },

    
        
     

  }
  
</script>

